body {
  max-width: 100%;
  padding: 20px;
  padding-top: 0px;
  font-family: 'PT Sans', sans-serif;
  background: -webkit-gradient(linear, left top, right top, color-stop(50%, #52071e), color-stop(50%, #380412));
  background: -webkit-linear-gradient(left, #52071e 50%, #380412 50%);
  background: -o-linear-gradient(left, #52071e 50%, #380412 50%);
  background: linear-gradient(to right, #52071e 50%, #380412 50%);
  color: #fff;
}

h1 {
  font-size: 50px;
}



